import { ElementPool } from "../../common/ElementPool";
import { BarElement, RangedSeriesView } from "../SeriesView";
export class BarRangeSeriesView extends RangedSeriesView {
    constructor(doc) {
        super(doc, 'rct-barrange-series');
        this._bars = new ElementPool(this._pointContainer, BarElement);
    }
    _getPointPool() {
        return this._bars;
    }
    _preparePointViews(doc, model, points) {
        this._bars.prepare(points.length, (v, i) => {
            v.point = points[i];
            v.setStyle('fill', points[i].color);
        });
    }
    _getLowValue(p) {
        return p.lowValue;
    }
    _layoutPointView(bar, i, x, y, wPoint, hPoint) {
        bar.wPoint = wPoint;
        bar.hPoint = hPoint;
        bar.layout(x, y + hPoint);
    }
}
