import { pickNum } from "../../common/Common";
import { calcPercent, parsePercentSize } from "../../common/Types";
import { DataPoint } from "../DataPoint";
import { PointItemPosition, RadialSeries, SeriesGroup, SeriesGroupLayout } from "../Series";
export class PieSeriesPoint extends DataPoint {
    constructor() {
        super(...arguments);
        this.sliced = false;
        this.startAngle = 0;
        this.angle = 0;
    }
    get endAngle() {
        return this.startAngle + this.angle;
    }
    legendColor() {
        return this.color;
    }
    legendLabel() {
        return this.x;
    }
    legendVisible() {
        return this.visible;
    }
    parse(series) {
        super.parse(series);
        this.sliced = this.source.sliced;
    }
}
export class PieSeries extends RadialSeries {
    constructor() {
        super(...arguments);
        this.groupSize = 1;
        this.innerSize = 0;
        this.sliceOffset = '7%';
        this.labelDistance = 25;
        this.exclusive = true;
        this.sliceDuration = 300;
        this.borderRadius = 0;
    }
    getInnerRadius(rd) {
        const dim = this._innerDim;
        return dim ? dim.size / (dim.fixed ? rd : 100) : 0;
    }
    getSliceOffset(rd) {
        return this._sliceDim ? calcPercent(this._sliceDim, rd) : 0;
    }
    getLabelPosition() {
        const p = this.pointLabel.position;
        return p === PointItemPosition.AUTO ? PointItemPosition.INSIDE : p;
    }
    _type() {
        return 'pie';
    }
    _colorByPoint() {
        return true;
    }
    _createPoint(source) {
        return new PieSeriesPoint(source);
    }
    getLegendSources(list) {
        this._visPoints.forEach(p => {
            list.push(p);
        });
    }
    _doLoad(src) {
        super._doLoad(src);
        this._innerDim = parsePercentSize(this.innerSize, true);
        this._sliceDim = parsePercentSize(this.sliceOffset, true);
    }
    _doPrepareRender() {
        super._doPrepareRender();
        this._groupPos = NaN;
    }
}
export class PieSeriesGroup extends SeriesGroup {
    constructor() {
        super(...arguments);
        this.polarSize = '80%';
        this.innerSize = 0;
    }
    getPolarSize(width, height) {
        return calcPercent(this._polarDim, Math.min(width, height));
    }
    getInnerRadius(rd) {
        const dim = this._innerDim;
        return dim ? dim.size / (dim.fixed ? rd : 100) : 0;
    }
    _seriesType() {
        return 'pie';
    }
    needAxes() {
        return false;
    }
    _canContain(ser) {
        return ser instanceof PieSeries;
    }
    _doLoad(source) {
        super._doLoad(source);
        this._polarDim = parsePercentSize(this.polarSize, true) || { size: 80, fixed: false };
        this._innerDim = parsePercentSize(this.innerSize, true);
    }
    _doPrepareSeries(series) {
        if (this.layout === SeriesGroupLayout.STACK || this.layout === SeriesGroupLayout.FILL) {
            const sum = series.map(ser => ser.groupSize).reduce((a, c) => a + pickNum(c, 1), 0);
            let p = 0;
            series.forEach((ser) => {
                ser._groupPos = p;
                p += ser._groupSize = pickNum(ser.groupSize, 1) / sum;
            });
        }
    }
}
