import { RcElement } from "../RcControl";
export class CircleElement extends RcElement {
    constructor(doc, styleName, cx, cy, radius) {
        super(doc, styleName, 'circle');
        if (typeof cx === 'number') {
            this.setCircle(cx, cy, radius);
        }
    }
    setCircle(cx, cy, radius) {
        this.setAttrs({
            cx: cx,
            cy: cy,
            r: radius
        });
    }
}
