import { ChartItem } from "./ChartItem";
export class Tooltip extends ChartItem {
    constructor(series) {
        super(series.chart);
        this.series = series;
        this.text = '<b>${point.x}</b><br>${series}:<b> ${point.y}</b>';
        this.offset = 8;
        this.hideDelay = Tooltip.HIDE_DELAY;
        this.minWidth = 100;
        this.minHeight = 40;
    }
    getValue(point, param) {
        switch (param) {
            case 'series':
            case 'series.name':
                return this.series.displayName();
            case 'point.x':
                return point.x;
            case 'point':
            case 'point.y':
                return point.y;
            default:
                return param;
        }
    }
}
Tooltip.HIDE_DELAY = 700;
