import { pickNum } from "../../common/Common";
import { LineType } from "../ChartTypes";
import { DataPoint } from "../DataPoint";
import { LineSeriesBase } from "./LineSeries";
export class ParetoSeriesPoint extends DataPoint {
    parse(series) {
        super.parse(series);
        this.xValue = this.x;
        this.yValue = this.y;
    }
}
export class ParetoSeries extends LineSeriesBase {
    constructor() {
        super(...arguments);
        this.curved = false;
    }
    _type() {
        return 'pareto';
    }
    getLineType() {
        return this.curved ? LineType.SPLINE : LineType.DEFAULT;
    }
    _createPoint(source) {
        return new ParetoSeriesPoint(source);
    }
    _referOtherSeries(series) {
        if (series.name === this.source || series.index === this.source) {
            series.referBy(this);
            return true;
        }
    }
    reference(other, axis) {
        if (!axis._isX) {
            this.$_loadPoints(other._visPoints);
            this.collectValues(this._xAxisObj, this._xAxisObj._values);
            this.collectValues(this._yAxisObj, this._yAxisObj._values);
        }
    }
    $_loadPoints(pts) {
        const list = [];
        const sum = pts.reduce((a, c) => a + pickNum(c.yValue, 0), 0);
        let acc = 0;
        pts.forEach(p => {
            if (!p.isNull) {
                list.push({
                    x: p.xValue,
                    y: acc += p.yValue * 100 / sum
                });
            }
        });
        this._doLoadPoints(list);
        this._visPoints = this._points.getVisibles();
    }
}
