export class Point {
    static empty() {
        return new Point();
    }
    static create(x = 0, y = 0) {
        return new Point(x, y);
    }
    constructor(x = 0, y = 0) {
        this.x = x;
        this.y = y;
    }
    get isEmpty() {
        return this.x === 0 || this.y === 0;
    }
    clone() {
        return new Point(this.x, this.y);
    }
    equals(sz) {
        return sz === this
            || sz && this.x === sz.x && this.y === sz.y;
    }
    setEmpty() {
        this.x = this.y = 0;
        return this;
    }
    set(x, y) {
        this.x = x;
        this.y = y;
        return this;
    }
    round() {
        const pt = this.clone();
        pt.x >>>= 0;
        pt.y >>>= 0;
        return pt;
    }
    toString() {
        return "{x: " + this.x + ", y: " + this.y + "}";
    }
}
