import { ElementPool } from "../../common/ElementPool";
import { PathElement } from "../../common/RcControl";
import { ArrowHead } from "../../model/series/VectorSeries";
import { SeriesView } from "../SeriesView";
import { SeriesAnimation } from "../animation/SeriesAnimation";
class ArrowView extends PathElement {
    constructor(doc) {
        super(doc, SeriesView.POINT_CLASS + ' rct-vector-point');
    }
    layout(headType, rotation, inverted) {
        const len = this.point._len;
        const off = this.point._off;
        const body = 1 / 2;
        let pts;
        switch (headType) {
            case ArrowHead.NONE:
                pts = [
                    0, -body * len,
                    0, body * len
                ];
                break;
            case ArrowHead.OPEN:
                const w2 = 1.5 / 10;
                const h2 = 4.5 / 10;
                const h3 = 2 / 10;
                pts = [
                    0, -h2 * len,
                    -w2 * len, -h3 * len,
                    0, -body * len,
                    w2 * len, -h3 * len,
                    0, -h2 * len,
                    0, body * len
                ];
                break;
            default:
                const w = 1 / 10;
                const h = 3 / 10;
                pts = [
                    0, -h * len,
                    -w * len, -h * len,
                    0, -body * len,
                    w * len, -h * len,
                    0, -h * len,
                    0, body * len
                ];
                break;
        }
        const path = ['M', pts[0], pts[1] + off];
        for (let i = 2; i < pts.length; i += 2) {
            path.push('L', pts[i], pts[i + 1] + off);
        }
        this.rotation = rotation;
        this.setPath(path);
    }
}
export class VectorSeriesView extends SeriesView {
    constructor(doc) {
        super(doc, 'rct-vector-series');
        this._arrows = new ElementPool(this._pointContainer, ArrowView);
    }
    _getPointPool() {
        return this._arrows;
    }
    _prepareSeries(doc, model) {
        const pts = model.getPoints().getVisibles();
        this.$_prepareArrows(pts);
    }
    _renderSeries(width, height) {
        const series = this.model;
        const start = series.startAngle;
        const head = series.arrowHead;
        const xAxis = series._xAxisObj;
        const yAxis = series._yAxisObj;
        this._arrows.forEach(v => {
            const p = v.point;
            if (v.setVisible(!p.isNull)) {
                const x = p.xPos = xAxis.getPosition(this.width, p.xValue);
                const y = p.yPos = this.height - yAxis.getPosition(this.height, p.yValue);
                v.translate(x, y);
                v.layout(head, p.angleValue + start, false);
            }
        });
    }
    _runShowEffect(firstTime) {
        firstTime && SeriesAnimation.fadeIn(this);
    }
    $_prepareArrows(pts) {
        this._arrows.prepare(pts.length, (v, i) => {
            v.point = pts[i];
            v.setStyle('stroke', v.point.color);
        });
    }
}
