export class Size {
    static empty() {
        return new Size();
    }
    static create(w = 0, h = 0) {
        return new Size(w, h);
    }
    constructor(width = 0, height = 0) {
        this.width = width;
        this.height = height;
    }
    get isEmpty() {
        return this.width === 0 || this.height === 0;
    }
    clone() {
        return new Size(this.width, this.height);
    }
    equals(sz) {
        return sz === this
            || sz && this.width === sz.width && this.height === sz.height;
    }
    setEmpty() {
        this.width = this.height = 0;
        return this;
    }
    set(width, height) {
        this.width = width;
        this.height = height;
        return this;
    }
    round() {
        const sz = this.clone();
        sz.width >>>= 0;
        sz.height >>>= 0;
        return sz;
    }
    toString() {
        return "{width: " + this.width + ", height: " + this.height + "}";
    }
}
Size.EMPTY = new Size();
