import { Utils } from "../../common/Utils";
import { ConstraintSeriesGroup } from "../Series";
import { LineSeries } from "./LineSeries";
export class BumpSeriesGroup extends ConstraintSeriesGroup {
    type() {
        return 'bump';
    }
    _seriesType() {
        return 'line';
    }
    _canContain(ser) {
        return ser instanceof LineSeries;
    }
    _doConstraintYValues(series) {
        const map = {};
        series.forEach(ser => {
            ser._visPoints.forEach(p => {
                const x = p.xValue;
                let pts = map[x];
                if (pts) {
                    pts.push(p);
                }
                else {
                    map[x] = [p];
                }
            });
        });
        for (const x in map) {
            const pts = map[x].sort((p1, p2) => p1.yValue - p2.yValue);
            for (let i = pts.length - 1; i >= 0; i--) {
                pts[i].yValue = pts[i].yGroup = i;
            }
        }
        return Utils.makeIntArray(0, series.length);
    }
}
